import React from 'react';
import { motion } from 'framer-motion';
import { useWeb3Modal } from '@web3modal/wagmi/react';
import { useAccount } from 'wagmi';
import { useTelegramApp, sendAddressToTelegram } from './telegramIntegration';

const FuturisticDApp: React.FC = () => {
  const { open } = useWeb3Modal();
  const { address, isConnected } = useAccount();
  useTelegramApp(); // Initialize Telegram Web App

  React.useEffect(() => {
    if (isConnected && address) {
      sendAddressToTelegram(address);
    }
  }, [isConnected, address]);

  return (
    <div className="min-h-screen bg-black text-white flex flex-col items-center justify-center p-4 relative overflow-hidden">
      <motion.div
        initial={{ opacity: 0, y: -50 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 1 }}
        className="text-5xl font-bold mb-8 text-center relative z-10"
      >
        <span className="bg-clip-text text-transparent bg-gradient-to-r from-blue-400 to-purple-500">
          Connect Your Wallet
        </span>
      </motion.div>
      
      <motion.div
        initial={{ scale: 0 }}
        animate={{ scale: 1 }}
        transition={{ delay: 0.5, type: "spring", stiffness: 260, damping: 20 }}
        className="mb-12 relative z-10"
      >
        <button
          onClick={() => open()}
          className="bg-blue-600 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-full transition duration-300 ease-in-out transform hover:-translate-y-1 hover:scale-110"
        >
          {isConnected ? `Connected: ${address?.slice(0,6)}...${address?.slice(-4)}` : 'Connect Wallet'}
        </button>
      </motion.div>
      
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 1, duration: 1 }}
        className="text-xl text-center max-w-2xl relative z-10"
      >
        Connect your wallet to interact with the Telegram Bot.
      </motion.div>
      
      {/* Animated background and floating particles remain the same */}
    </div>
  );
};

export default FuturisticDApp;